<template>
  <div style="text-align: center;">
    <div style="float: left;">
      <a-icon class="trigger" :type="collapsed ? 'menu-unfold' : 'menu-fold'" @click="$emit('toggleCollapsed')" />
    </div>
    
    <a style="color: #50799e;font-weight: bold;font-size: 20px;">{{ ProjectName }}</a>

    <div style="float: right;">
      <a-dropdown :trigger="['click']">
       
        <span class="trigger" style="color: #50799e;">
          <img :src="avatar" width="36" style="margin-top: -6px; margin-left: 8px;" />
          {{username}}
          <span v-if="isAdmin"> 管理员 </span>
          <span v-else-if="isDepartmentAdmin"> 部门管理员 </span>
          <span v-else-if="isTeamAdmin"> 小组管理员 </span>
          <span v-else> 被管理员 </span>

          <a-icon type="down" style="margin-left: 10px;" />
        </span>
        <a-menu slot="overlay" >
          <a-menu-item @click="$router.push('/user/set_password')">
            <span>修改密码</span>
          </a-menu-item>
          <a-menu-divider />
          <a-menu-item @click="logout">
            <span>退出登录</span>
          </a-menu-item>
        </a-menu>
      </a-dropdown>
    </div>
  </div>
</template>

<script>
  // import { warehouseOption } from '@/api/option';
  import Cookies from 'js-cookie';

  export default {
    name: 'Headbar',
    props: ['collapsed', 'username'],
    data() {
      return {
        warehouseItems: [],
        currentWarehouse: '所有仓库',
        avatar:require('@/assets/avatar.png')
      };
    },
    computed: {
      isAdmin() {
        return this.$store.state.user.is_admin
      },
      isDepartmentAdmin() {
        return this.$store.state.user.is_department_admin
      },
      isTeamAdmin() {
        return this.$store.state.user.is_team_admin
      },
    },
    methods: {
      initData() {

      },
      logout() {
        Cookies.remove('access');
        Cookies.remove('refresh');
        this.$router.push('/user/login');
      },
    },
    mounted() {
      this.initData();
    },
  }
</script>

<style scoped>
  .trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    transition: color 0.3s;
  }

  .trigger:hover {
    color: #1890ff;
  }
</style>